.main_page {
    padding: 40px;
    height: 100vh;
    display: flex;
    justify-content: space-between;
}

.main_data {
    width: 80%;
    display: flex;
    justify-content: space-between;

    .data_main {
        width: 24%;
        display: inline-block;
        font-size: 14px;
        font-family: 'Inter', sans-serif;
        padding: 20px;
        height: 107px;
        border-radius: 8px;      
        color: inherit;
        text-align: left;
        h1 {
            font-size: 48px;
            font-weight: 700;
            font-family: 'Inter', sans-serif;
            margin: 0;
            margin-top: 5px;
        }
    }
}