.dialog {
  border: none;
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center 75px;
  padding: 0;

  &::before {
    content: 'master.place';
    font-size: 48px;
    margin-top: 200px;
    display: block;
    font-weight: 500;
    font-family: 'Rubik', sans-serif;
  }
  form {
    margin: 50px auto;
    width: 328px;
    height: auto;
    display: block;
    border-radius: 8px;
    border: 1px solid #F5F5F5;
    background-color: #F5F5F5;
    padding-bottom: 20px;

    input {
      display: block;
      margin: 20px auto 0;
      padding: 20px;
      width: calc(100% - 40px);
      height: 42px;
      border: none;
      min-width: 200px;
      border-radius: 8px;
      outline: none;
    }

    input[type=submit] {
      background-color: #3D4EEA;
      font-weight: 700;
      color: #fff;
      cursor: pointer;
      line-height: 8px;
    }
  }
}