@mixin span_font($font: 16px) {
    font-size: $font;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
}

.main_locked {
    width: calc(82% - 40px);
    height: 336px;
    padding: 0;

    text-align: left;

    .find_number {
        display: grid;
        gap: 20px 16px;
        width: 100%;
        grid-template-columns: 60px auto 108px 200px;
        // justify-content: space-between;
        height: 75px;
        align-items: center;
        border-radius: 8px;
        padding: 0 16px;

        button {

            border-radius: 8px;
            @include span_font(18px);
            height: 42px;

            border: none;
        }

        span {
            @include span_font
        }

        input {
            display: inline-block;
            // width: calc(100% - 200px);
            height: 42px;
            padding: 18px;
            border-radius: 8px;
            font-family: 'Inter', sans-serif;
        }
    }

}

.main_modal {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: rgba(64, 64, 64, 0.60);
    backdrop-filter: blur(10px);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    .modal {
        display: flex;
        width: 720px;
        height: 268px;
        flex-direction: column;
        padding: 40px 20px;
        align-items: center;
        gap: 20px;
        border-radius: 8px;
        justify-content: space-between;

        p, h4 {
            margin: 0;
            font-size: 18px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            width: 60%;
        }
        h4 {
            color: #3D4EEA;
        }

        button {
            font-size: 18px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            margin-left: 10px;
            padding: 10px 20px;
            
        }

    }
}