.main_message {
    width: calc(82% - 40px);
    height: 336px;
    padding: 16px;
    text-align: left;

    h2 {
        font-size: 18px;
        font-weight: 700;
        font-family: 'Inter', sans-serif;
        text-align: left;
        margin: 0;
    }

    textarea {
        display: block;
        height: 200px;
        padding: 10px;
        border: none;
        margin: 20px 0;
        width: 100%;
        border-radius: 8px;
        outline: none;
    }

    button {
        padding: 10px 20px;
        margin-right: 20px;
        font-size: 18px;
        font-family: Inter;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }
}