.main_block {
    display: flex;
    justify-content: flex-start;
    margin: 20px;
    width: calc(100% - 40px);
    flex-wrap: wrap;
    border: 1px solid #ddd;
}

.main_block img {
    width: 200px;
    height: auto;
    
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.5s;
}

.main_block img:hover {
    box-shadow: 2px 2px 12px 2px #ddd;
}
.big_image {
    position: absolute;
    width: 40%;
    height: auto;
    top: 50px;
    left: 30%;
}
.big_image div {
    display: flex;
    justify-content: space-between;
}
.big_image div button {
    width: 48%;
    height: 40px;
    display: inline-block;
    margin-top: 10px;
    border: none;
    background-color: blueviolet;
    color: #fff;
    cursor: pointer;
}
.big_image img {
    width: 100%;
}