.main {
    width: 18%;
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.navigation {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.pending,
.active,
.tema_selector {
    padding: 10px 20px;
    border-radius: 8px;
    background: #F5F5F5;
    font-family: 'Inter', sans-serif;
    font-size: 18px;
    text-align: left;
    margin-bottom: 12px;
    text-decoration: none;
    color: #000;
}

.active {
    background: #3D4EEA;
    color: #fff;
    display: inline-block;
}

.tema_selector {
    font-weight: 500;
    font-size: 16px;
    padding: 12px;
    line-height: 25px;
    margin: 0;

    .black,
    .white {
        height: 25px;
        border-radius: 40px;
        width: 46px;
        border: 1px solid black;
        float: right;
        text-align: center;

        b {
           
            display: block;
            height: 21px;
            width: 21px;
            background-color: #000;
            border-radius: 50%;
            margin-left: 2px;
            margin-top: 0.8px;
        }
    }

    .white {
       b {
            margin-left: 21px;
        }
    }
}