@mixin span_font($font: 16px) {
    font-size: $font;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
   }
a.back {
    float: left;
    padding: 8px 16px;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 500;
    font-family: 'Inter', sans-serif;

    img {
        margin-right: 10px;

    }
}

.user_data {
    height: 218px;
    padding: 20px;
    margin: 42px 0 0;
    width: 100%;
    display: flex;
}

.user_data_personal {
    width: calc(100% - 200px);
   display: flex;
   font-family: 'Inter', sans-serif;
}
.user_data_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: left center;
    width: 178px;
    height: 178px;
    border-radius: 8px;
    margin-right: 20px;
}
.user_data_text {
    text-align: left;
    display: flex;
    width: 70%;
    flex-direction: column;
    justify-content: space-between;
    div {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        h2 {
            margin: 0;
            text-transform: uppercase;
            font-size: 18px;
            font-family: 'Inter', sans-serif;
            font-weight: 700;
        }
        img {
            margin-left: 8px;           
        }
        a {
            color: #3D4EEA;
        }
        p {
            margin: 0;
        }
        span {
            font-size: 14px;
            margin-right: 5%;
            line-height: 21px;            
            color: inherit;
            display: inline-block;
            width: 25%;
        }
    
        p:last-of-type {
            span {
                font-size: 16px;
                font-weight: 700;
            }
        }
    }
}
.user_review {
    @include span_font('18px');
    text-align: left;
    margin:20px;
}

.user_data_buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    button {
        width: 196px;
        font-size: 16px;
        font-family: Inter;
        font-style: normal;
        font-weight: 700;
        border-radius: 4px;
        padding: 8px 12px;
        line-height: normal;
        letter-spacing: 0.8px;
        border-width: 1px;
        border-style: solid;
    }
}
.user_review_total {
    width: 343px;
    height: 174px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
    p {
        margin: 0 ;
        display: flex;
        justify-content: space-between;
        align-items: center;
        b{
            font-size: 16px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.8px; 
        }
        span:last-of-type {
            font-size: 14px;
            font-weight: 500;
            font-family: 'Inter', sans-serif;
            }
        
    }
    
    h6 {
        font-size: 12px;
        line-height: 18px;
        font-weight: 400;
        text-align: left;
        margin: 12px 0;
    }
    h5 {
        margin: 0;
        cursor: pointer;
    }
    a, h5 {
        color: #3D4EEA;
        font-family: 'Inter', sans-serif;
        img {
            margin-left: 5px;
        }
    }
}
.main_user_review {
    display: flex;
}