.dialogs {
    grid-template-columns: 147px auto 108px;
}

.all_dialogs {
    display: flex;
    justify-content: space-between;
    margin-top: 12px;
}

.dialogs_users {
    width: 343px;

}

.select_user_dialog {
    width: calc(100% - 355px);
    border-left: 2px solid #F5F5F5;
}

.dialog_user {
    padding: 8px;
    border-radius: 8px;
    padding: 8px;
    margin-bottom: 10px;
    cursor: pointer;
    p {
        margin: 0;
        display: flex;
        color: inherit;
        justify-content: space-between;

        b {
            font-size: 16px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.8px;
        }
    }
}
.dialog_text {
    width: 60%;
    border-radius: 8px;
    margin: 0  0 12px 10px;
    padding: 20px;
    p {
       margin-bottom: 16px;
    }
    p:last-of-type {
        margin: 0;
    }
}
