@mixin span_font($font: 16px) {
   font-size: $font;
   font-weight: 700;
   font-family: 'Inter', sans-serif;
  }
.main_user {
    width: calc(82% - 40px) ;
    position: relative;
}
.find_user {
    display: grid;
    gap: 20px 16px;
    width: 100%;
    grid-template-columns: 60px auto 108px;   
    height: 75px;
    align-items: center;
    border-radius: 8px;
    padding: 0 16px;
    button {       
        border-radius: 8px;
        @include span_font(18px);
        height: 42px;        
        border: none;
    }
    span {
       @include span_font
    }
    input {
        display: inline-block;        
        height: 42px;
        padding: 18px;
        border-radius: 8px;
        font-family: 'Inter', sans-serif;
    }
}
.user_header {
    display: flex;
    justify-content: flex-start;
    padding:0 10px;
    margin: 27px 0 10px;
    span {
        display: inline-block;
        color: inherit;
        width: 20%;        
        text-align: left;
        font-size: 14px;
    }
}
.one_user {
    margin: 4px 0 0;
    padding: 8px;
    height: 44px;
    border-radius: 8px;
    position: relative;
    span {
        line-height: 28px;
        font-weight: 500;
        font-size: 16px;
    }
    a, button {
        position: absolute;
        right: 8px;
        width: 176px;
        height: 28px;
        top: 8px;
        @include span_font;
        border-radius: 8px;
        line-height: 28px;
       
    }
    button {
        width: 90px;
        border-radius: 4px;
        &:hover {
            background-color: #EA3D3D;
            color: #fff;
        }
    }    

}
.users_all {
    height: calc(100vh - 277px);
    overflow: hidden;
}
