@mixin span_font($font: 16px) {
    font-size: $font;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
}

.pages {
    position: absolute;
    bottom: 40px;

    span {
        display: inline-block;
        padding: 4px 0;

        @include span_font;
        border-radius: 4px;
        margin-right: 10px;
        height: 28px;
        width: 28px;
        cursor: pointer;
        box-sizing: border-box;
    }
}